.container{
  background: url('./Wave.svg');
  background-repeat: no-repeat;
  background-position-x: center;
  background-size: cover;
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.logo{
  padding: 60px;
  width: 20%;
  cursor: pointer;
  transition: 0.5s;
}

.logo:hover{
  transform: scale(1.2) rotate(-5deg);
}


.fa-chevron-down {
  color: #000000 !important;
  bottom: 10px;
  cursor: pointer;
  transition: 1.2s ease;
  margin-bottom: 200px;
}

/* RESPONSIVE */
@media (max-width: 1024px) {
  .logo{
    padding: 30px;
    width: 80%;
    cursor: pointer;
    transition: 0.5s;
  }
}